import { IonCard, IonCol, IonContent, IonGrid, IonHeader, IonLabel, IonPage, IonRow, IonSegment, IonSegmentButton, IonToolbar } from '@ionic/react';

import "./BookingPage.css"

const BookingPage: React.FC = () => {
  return (
      <IonPage>
        <IonHeader>
          <IonToolbar>
            <IonLabel>Booking List</IonLabel>
          </IonToolbar>
          <IonSegment mode="md">
              <IonSegmentButton>
                <IonLabel className="segment-label">Paid</IonLabel>
              </IonSegmentButton>
              <IonSegmentButton>
                <IonLabel className="segment-label">Check In</IonLabel>
              </IonSegmentButton>
              <IonSegmentButton>
                <IonLabel className="segment-label">Done</IonLabel>
              </IonSegmentButton>
              <IonSegmentButton>
                <IonLabel className="segment-label">Canceled</IonLabel>
              </IonSegmentButton>
            </IonSegment>
        </IonHeader>
        <IonContent>
          <IonCard className="book-card">
            <IonLabel>Plaza Candi</IonLabel>
            <br />
            <hr />
            <IonLabel>Meeting Room 1</IonLabel>
            <br />
            <br />
            <IonGrid className="book-detail">
              <IonRow>
                <IonCol>
                  <IonLabel color="medium">Date Booking</IonLabel>
                </IonCol>
                <IonCol>
                  <IonLabel color="medium">Check in Before</IonLabel>
                </IonCol>
              </IonRow>
              <IonRow>
                <IonCol>
                  <IonLabel color="secondary">Wednesday, 06 - 10 - 2021</IonLabel>
                </IonCol>
                <IonCol>
                  <IonLabel color="secondary">08:00</IonLabel>
                </IonCol>
              </IonRow>
            </IonGrid>
          </IonCard>
        </IonContent>
      </IonPage>
  );
};

export default BookingPage;
