import React from "react";

export const Context = React.createContext<any>(undefined);

export const UseProvider: React.FC = ({ children }) => {
    const [cardContext, setCardContext] = React.useState({
        image: "",
        location: "Alcott Semarang",
        rates: "",
        category1: "",
        category2: "",
        category3: ""
    })

    let allContext = {
        cardContext,
        setCardContext
    }

    return <Context.Provider value={allContext}>{children}</Context.Provider>
};

export default Context;